import goDetail from "@/components/goDetail.vue";
export default {
	components:{goDetail},
	data(){
		return {
			params:{
				limit:30,
				order:"new",
				skip:0,
				first:1
				/* 1 返回值中有album对象 表示第一次请求
				   0 返回值中只有wallpaper数组，不是第一次请求*/
			},
			id:-1,
			album:{},
			wallpaper:[],
			hasMore:true
		}
	},
	onLoad(options) {
		console.log(options);
		this.id=options.id;
		/* this.id="5d5f8e45e7bce75ae7fb8278"; */
		this.getList();
	},
	/* 页面触底，上拉加载下一页 */
	onReachBottom(){
		console.log("123");
		if(this.hasMore){
			this.params.first=0;
			this.params.skip+=this.params.limit;
			this.getList();
		}else{
			uni.showToast({
				title:"没有更多了",
				icon:"none"
			})
		}
	},
	methods:{
		getList(){
			this.request({
				url: `http://157.122.54.189:9088/image/v1/wallpaper/album/${this.id}/wallpaper`,
				data:this.params
				
			})
			.then(result=>{
				console.log(result);
				if(Object.keys(this.album).length===0)
				{this.album=result.res.album;}
				if(result.res.wallpaper.length===0){
					this.hasMore=false;
					uni.showToast({
						title:"没有更多了",
						icon:"none"
					});
					return;
				}
				this.wallpaper=[...this.wallpaper,...result.res.wallpaper];
				
			})
		}
	}
}
